import React from 'react';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
} from "react-router-dom";

import Home from './Home';
import Faq from './Faq';
import Logout from './Logout';
import Profile from './Profile';
import Settings from './Settings';
import Team from './Team';
import Upload from './Upload';

function Header(){
  return(
    <Router>

    <header>
    <nav id="navbar-main" class="navbar navbar-main navbar-expand-lg navbar-transparent  py-2">
        <div class="container">
          <Link to="/">
          <a class="navbar-brand mr-lg-5">
            <img src="assets/img/brand/ngwhite.png" alt=""></img>
          </a>
          </Link>
          <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbar_global" aria-controls="navbar_global" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
          </button>
          <div class="navbar-collapse collapse" id="navbar_global">
            <div class="navbar-collapse-header">
              <div class="row">
                <div class="col-6 collapse-brand">
                  <Link to="/"> <img src="assets/img/brand/ngblack.png" alt=""></img>  </Link>
                    
                </div>
                <div class="col-6 collapse-close">
                  <button type="button" class="navbar-toggler" data-toggle="collapse" data-target="#navbar_global" aria-controls="navbar_global" aria-expanded="false" aria-label="Toggle navigation">
                    <span></span>
                    <span></span>
                  </button>
                </div>
              </div>
            </div>
            <ul class="navbar-nav navbar-nav-hover align-items-lg-center ml-lg-auto">
              <li class="nav-item">
              <Link to="/"><a class="btn btn-white">Home</a></Link>
              </li>
              <li class="nav-item">
              <Link to={{ pathname: "https://www.shopdisney.com/franchises/national-geographic/" }} target="_blank"><a class="btn btn-white">Shop</a></Link>
              </li>
              <li class="nav-item">
              <Link to="/Profile"><a class="btn btn-white">Profile</a></Link>
              </li>
              <li class="nav-item">
              <Link to="/Team"><a class="btn btn-white">Team</a></Link>
              </li>
              <li class="nav-item">
              <Link to="/Upload"><a class="btn btn-white">Upload</a></Link>
              </li>
              <li class="nav-item">
              <Link to="/Settings"><a class="btn btn-white">Settings</a></Link>
              </li>
              <li class="nav-item">
              <Link to="/Faq"><a class="btn btn-white">FAQ</a></Link>
              </li>
              
              <li class="nav-item">
              <Link to="/Logout"><a class="btn btn-white"><span class="nav-link-inner--text">Logout</span></a></Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      
      </header>

    <Switch>
      <Route exact path="/"><Home/></Route>
      <Route exact path="/Faq"><Faq/></Route>
      <Route exact path="/Logout"><Logout/></Route>
      <Route exact path="/Profile"><Profile/></Route>
      <Route exact path="/Settings"><Settings/></Route>
      <Route exact path="/Team"><Team/></Route>
      <Route exact path="/Upload"><Upload/></Route>
    
    </Switch>    

    </Router>
  );

}

export default Header;